<template>
    <div class="container">
        <h2>indexes 索引选择器组件</h2>
        <h3>索引选择器组件</h3>
        <Pcode :code="code">
        </Pcode>

        <h3>属性</h3>
        <PAttrTable :data="attrs"/>

        <h3>事件</h3>
        <PEventTable :data="events"/>
    </div>
</template>

<script>
import PAttrTable from '@/components/PAttrTable.vue'
import PEventTable from '@/components/PEventTable.vue'
import Pcode from '@/components/Pcode.vue'

export default {
    name: 'button',
    components: {
        PAttrTable, PEventTable, Pcode
    },
    data () {
        return {
            attrs: [
                {
                    param: 'list',
                    desc: '索引选择器数据',
                    type: 'String',
                    optional: '-',
                    default: '-'
                },
                {
                    param: 'value',
                    desc: '列表选中值的id',
                    type: 'Array',
                    optional: '-',
                    default: '默认返回数据里的id'
                },
                {
                    param: 'thumb',
                    desc: '列表选中值左侧显示图标',
                    type: 'String(图片地址)',
                    optional: '-',
                    default: '-'
                },
                {
                    param: 'defaultKeyProps',
                    desc: '自定义需要显示的数据key值',
                    type: 'Object',
                    optional: '-',
                    default: '{titleKey:\'title\',intoKey:\'key\',listKey:\'items\'}'
                }
            ],
            events: [
                {
                    name: 'onClick',
                    desc: `点击列表项触发事件`,
                    param: `[value,value]`
                }
            ],
            code: `
<script>
    import Taro, { Component } from '@tarojs/taro'
    import { View } from '@tarojs/components'
    import {
        WiIndexes,
        WiDrawer
    } from 'hosjoy-wi-ui'

    export default class Index extends Component {
        constructor(props){
            super(props)
            this.state = {
                show:false,
                value:[]
            }
        }

        onCloseTest(){
            console.log('关闭后执行')
        }

        onClickItem(item){
            console.log(item,'列表被点击了啊')
            const { value } = this.state
            let arr = value
            let index = arr.indexOf(item.id);
            if (index > -1) {
                arr.splice(index, 1);
            } else {
                arr.push(item.id)
            }
            this.setState({
                value:arr
            })
        }
        onShow(){
            this.setState({
                show:true
            })
        }

        render () {
          const { show,value } =  this.state
          const list = [
              {
                  letter: 'A',
                  letterList: [
                      {
                          name: '阿坝',
                          id:1
                      },
                      {
                          name: '阿拉善',
                          id:2
                      }]
              },
              {
                  letter: 'B',
                  letterList: [
                      {
                          name: '北京',
                          id:4
                      },
                      {
                          name: '保定',
                          id:5
                      }]
              },
              {
                  letter: 'C',
                  letterList: [
                      {
                          name: '滁州',
                          id:6
                      },
                      {
                          name: '保定',
                          id:7
                      }]
              }
          ]
          const _props = {
              titleKey:'letter',
              intoKey:'letter',
              listKey:'letterList'
          }
          return (
              <View className='index'>
                  <View onClick={()=>this.onShow()}>弹出</View>
                  <WiDrawer
                      show={show}
                      onClose={this.onCloseTest}
                  >
                      <WiIndexes
                          list={list}
                          defaultKeyProps={_props}
                          onClick={this.onClickItem.bind(this)}
                          thumb="https://hosjoy-hbp.oss-cn-hangzhou.aliyuncs.com/images/20200224/90534805-1506-4a90-bdfc-9086de19d7fb.png"
                          value={value}
                      />
                  </WiDrawer>
              </View>
          )
        }
}
<\/script>`
        }
    },
}
</script>

<style lang="scss" scoped>
.container {
    padding-bottom: 50px;
}
</style>
